.doc-main .content-article .list_job .job-item{position:relative;display:grid;grid-template-columns:400px 300px;align-items:center;height:86px;padding:18px 0 20px;box-shadow:-1px 2px 5px rgba(0,0,0,.25);box-sizing:border-box}.doc-main .content-article .list_job .job-item+li{margin-top:7px}.doc-main .content-article .list_job .new::before{position:absolute;left:0;top:0;z-index:1;display:flex;justify-content:center;align-items:center;width:3px;height:100%;font-size:10px;color:#fff;font-weight:bold;letter-spacing:-1px;background-color:#fd0;content:"NEW"}.doc-main .content-article .list_job .new::after{position:absolute;top:24px;left:-11px;font-family:"Font Awesome 5 Free";font-weight:900;margin-right:10px;font-size:25px;color:#fd0;content:""}.doc-main .content-article .list_job img{display:block;margin:0 auto}.doc-main .content-article .title_job{margin-left:20px;padding:0 5px}.doc-main .content-article .title_job strong{display:inline-block;font-size:16px;font-weight:bold;line-height:21px}.doc-main .content-article .title_job em{display:inline-block;margin-left:10px;font-size:12px;color:#999}.doc-main .content-article .title_job .link_title{display:inline-block}.doc-main .content-article .title_job .link_title .highlight{background-color:#fd0}.doc-main .content-article .title_job .bundle_badge{overflow-x:scroll;display:block;margin-top:5px;font-size:0;white-space:nowrap;-ms-overflow-style:none;scrollbar-width:none}.doc-main .content-article .title_job .bundle_badge::-webkit-scrollbar{display:none}.doc-main .content-article .title_job .bundle_badge a{display:inline-block;height:21px;padding:0 8px;font-size:10px;color:gray;line-height:21px;background-color:#f0f0f0;border-radius:3px;box-sizing:border-box}.doc-main .content-article .title_job .bundle_badge a+a{margin-left:5px}.doc-main .content-article .title_job .bundle_badge a .highlight{background-color:#fd0}.doc-main .content-article .desc_job{padding:0 25px 0 5px;text-align:right}.doc-main .content-article .desc_job .badge_type{display:inline-block;height:21px;padding:0 7px;font-size:10px;font-weight:bold;color:#fff;line-height:21px;border-radius:3px;background-color:#37f}.doc-main .content-article .desc_job .wrap_txt{margin-top:9px;font-size:0}.doc-main .content-article .desc_job .wrap_txt span+span{margin-left:10px}.doc-main .content-article .desc_job .txt_day{display:inline-block;font-size:12px}.doc-main .content-article .desc_job .txt_day::before{font-family:"Font Awesome 5 Free";font-weight:400;margin-right:5px;font-size:10px;content:""}.doc-main .content-article .desc_job .txt_location{display:inline-block;font-size:12px}.doc-main .content-article .desc_job .txt_location::before{font-family:"Font Awesome 5 Free";font-weight:900;margin-right:5px;font-size:10px;content:""}