body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,button{margin:0;padding:0}fieldset,img{border:0 none}dl,ul,ol,menu,li{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}input,select,textarea,button{vertical-align:middle}input::-ms-clear{display:none}button{border:0 none;background-color:rgba(0,0,0,0);cursor:pointer}body{background:#fff}body,th,td,input,select,textarea,button{font-size:12px;line-height:1.5;font-family:"돋움",dotum,sans-serif;color:#3d5866;outline:none}a{color:#333;text-decoration:none}a:active{background-color:rgba(0,0,0,0)}address,caption,cite,code,dfn,em,var{font-style:normal;font-weight:normal}